import * as actionTypes from './actionTypes';
import { fromJS } from 'immutable';

const defaultState = fromJS({
	title: '',	// 
	content: '', 	//
	author: '',	//
	avatar: '', //
	date: '',	 //
	time: '',  //
	authorlist: [], //
	reclist: [], //
	scroll: false,
	inDetail: false
});

const getDetailData = (state, action) => {
	return state.merge({
		title: fromJS(action.data.title),	// 
		content: fromJS(action.data.content), 	//
		author: fromJS(action.data.author),	//
		avatar: fromJS(action.data.avatar), //
		date: fromJS(action.data.date),	 //
		time: fromJS(action.data.time),  //
		authorlist: fromJS(action.data.authorlist), //
		reclist: fromJS(action.data.reclist) //
	});
}

export default ( state = defaultState, action ) => {
	switch (action.type) {
		case actionTypes.GET_DETAIL_DATA: 
			return getDetailData(state, action);
		case actionTypes.SCROLL_Y:
			return state.set("scroll", action.data);
		case actionTypes.GET_IN_DETAIL:
			return state.set("inDetail", action.data);
		default:
			return state;
	}
}